import React, {Component}  from 'react';
import { Button } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

declare var ithours_client;

const divStyle = {
  color: '#00d2ee',
};

class Reference extends Component {

  constructor(props) {
    super(props);
    this.state = {
     referencename: '',
     referencenumber: '', 
     updateText : 'Update'       
    }
   }
    async  componentWillMount() {
      var borrower_id = window.localStorage.getItem("BID");
      let data = await ithours_client.getOne("borrower", {
          _id: borrower_id,
      });
      console.log(data);
      this.state.referencename = data.apidata.Data.referencename;
      this.state.referencenumber = data.apidata.Data.referencenumber;
      this.setState({})
      }
      async update() {
        toast.dark("Update successful", {
            position:'bottom-right',
            autoClose:5000,
            hideProgressBar:true,
        });
        this.state.updateText = 'Updating....';
        this.setState({})
        console.log(this.state);
        var borrower_id = window.localStorage.getItem("BID");
        var findQuery = {
            _id: borrower_id,
        };
        var updateQuery = {
          referencename: this.state.referencename,
          referencenumber: this.state.referencenumber,
        }
        let updatedRes = await ithours_client.update("borrower", findQuery, updateQuery);
        console.log(updatedRes);
        this.state.updateText = 'Update';
        this.setState({})
      }
      handleReferenceNameChange(e) {
        console.log(e);
        this.state.referencename = e.target.value;
        this.setState({   });
      };
      handleReferenceNumberChange(e) {
        console.log(e);
        this.state.referencenumber = e.target.value;
        this.setState({   });
      };
   render(){
    return (
      <div>
       <Form>
        <ToastContainer/>
          <Form.Group className="col-md-12" controlId="formBasicEmail">
                <h4 className="text-left  mt-1 mb-1" style={divStyle} >Reference 1 Details</h4>
              </Form.Group>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Family member's reference name</Form.Label>
                  <Form.Control type="first_name"  placeholder="Family member's reference name" onChange={(e) => this.handleReferenceNameChange(e)}  value={this.state.referencename} />
              </Form.Group>

             <Form.Group controlId="formBasicEmail">
               <Form.Label>Family member's reference number</Form.Label>
                  <Form.Control type="Number"  placeholder="Family member's reference number" onChange={(e) => this.handleReferenceNumberChange(e)}  value={this.state.referencenumber} />
              </Form.Group>
         <Form.Group className="col-md-12" controlId="formBasicEmail">
          <h4 className="text-left  mt-1 mb-1" style={divStyle}>Reference 2 Details</h4>
          </Form.Group>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Friend's reference name</Form.Label>
                  <Form.Control type="first_name"  placeholder="Friend's reference name" onChange={(e) => this.handleReferenceNameChange(e)}  value={this.state.referencename} />
              </Form.Group>

              <Form.Group controlId="formBasicEmail">
                <Form.Label>Friend's reference number</Form.Label>
                  <Form.Control type="Number"  placeholder="Friend's reference number" onChange={(e) => this.handleReferenceNumberChange(e)}  value={this.state.referencenumber} />
              </Form.Group>
              <div className="form-submit">
                 <Button className="cta-btn cta-blue" onClick={() => this.update()} >{this.state.updateText}</Button>
              </div>
          </Form>       
      </div>
     );
   }
}

export default Reference
