import React, {Component} from 'react';
import { Button } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

declare var ithours_client;

const divStyle = {
  color: '#00d2ee',
};
class Address extends Component {

  constructor(props) {
    super(props);
    this.state = {
     address: '',
     state: '',
     city:'',
     pincode:'',  
     updateText : 'Update'    
    }
}
  
  async componentDidMount() {
    var borrower_id = window.localStorage.getItem("BID");
    let data = await ithours_client.getOne("borrower", {
        _id: borrower_id,
    });
    console.log(data);
    this.state.address = data.apidata.Data.address;
    this.state.state = data.apidata.Data.state;
    this.state.city = data.apidata.Data.city;
    this.state.pincode = data.apidata.Data.pincode;
    this.setState({})
}
async update() {
  toast.dark("Update successful", {
      position:'bottom-right',
      autoClose:5000,
      hideProgressBar:true,
  });
  this.state.updateText = 'Updating....';
  this.setState({})
  console.log(this.state);
  var borrower_id = window.localStorage.getItem("BID");
  var findQuery = {
      _id: borrower_id,
  };
  var updateQuery = {
       address: this.state.address,
       state: this.state.state,
       city: this.state.city,
       pincode: this.state.pincode,
  }
  let updatedRes = await ithours_client.update("borrower", findQuery, updateQuery);
  console.log(updatedRes);
  this.state.updateText = 'Update';
  this.setState({})
}
handleAddressChange(e) {
  console.log(e);
  this.state.address = e.target.value;
  this.setState({   });
};
handleStateChange(e) {
  console.log(e);
  this.state.state = e.target.value;
  this.setState({   });
};
handleCityChange(e) {
  console.log(e);
  this.state.city = e.target.value;
  this.setState({   });
};
handlePinCodeChange(e) {
  console.log(e);
  this.state.pincode = e.target.value;
  this.setState({   });
};
   render(){
    return (
      <div>
          <Form>
           <ToastContainer/>
          <Form.Group className="col-md-12" controlId="formBasicEmail">
           <h4 className="text-left mt-1 mb-1" style={divStyle}>Permanent Address</h4>
          </Form.Group>
              <Form.Group controlId="formBasicEmail">
                   <Form.Label>Address</Form.Label>
                  <Form.Control type="first_name"  placeholder="Address" onChange={(e) => this.handleAddressChange(e)} value={this.state.address} />
              </Form.Group>
              <Form.Group controlId="exampleForm.ControlSelect1">
                   <Form.Label>State*</Form.Label>
                <Form.Control as="select" onChange={(e) => this.handleStateChange(e)} value={this.state.state}>
                  <option>Uttar Pradesh</option>
                  <option>Himachal Pradesh</option>
                  <option>Bihar</option>
                  <option>Andra Pradesh</option>
                  <option>Madhya Pradesh</option>
                  <option>Arunachal Pradesh</option>
                  <option>Panjab</option>
                  <option>Gujarat</option>
                </Form.Control>                
              </Form.Group>
              <Form.Group controlId="exampleForm.ControlSelect1">
                   <Form.Label>City*</Form.Label>
                <Form.Control as="select"
                onChange={(e) => this.handleCityChange(e)}
                value={this.state.city}
                options={this.state.city}    
                 >
                  <option>Noida</option>
                  <option>Delhi</option>
                  <option>Shimla</option>
                  <option>Vadodara</option>
                  <option>Heydrabad</option>
                  <option>Chandighar</option>
                  <option>Mohali</option>
                  <option>Gurogram</option>
                </Form.Control>                
              </Form.Group>
              <Form.Group controlId="formBasicEmail">
                   <Form.Label>Pin Code</Form.Label>
                  <Form.Control type="business_name"  placeholder="Pin Code" onChange={(e) => this.handlePinCodeChange(e)} value={this.state.pincode} />
              </Form.Group>
              <Form.Group className="col-md-12" controlId="formBasicEmail">
                <h4 className="text-left mt-1 mb-1" style={divStyle}>Current Address</h4>
          </Form.Group>
              <Form.Group controlId="formBasicEmail">
                   <Form.Label>Address</Form.Label>
                  <Form.Control type="first_name"  placeholder="Address" onChange={(e) => this.handleAddressChange(e)} value={this.state.address} />
              </Form.Group>
              <Form.Group controlId="exampleForm.ControlSelect1">
                   <Form.Label>State*</Form.Label>
                <Form.Control as="select" value={this.state.state} onChange={(e) => this.handleStateChange(e)}>
                  <option>Uttar Pradesh</option>
                  <option>Himachal Pradesh</option>
                  <option>Bihar</option>
                  <option>Andra Pradesh</option>
                  <option>Madhya Pradesh</option>
                  <option>Arunachal Pradesh</option>
                  <option>Panjab</option>
                  <option>Gujarat</option>
                  <option>Hariyana</option>
                </Form.Control>                
              </Form.Group>
              <Form.Group controlId="exampleForm.ControlSelect1">
                   <Form.Label>City*</Form.Label>
                <Form.Control as="select" value={this.state.city} onChange={(e) => this.handleCityChange(e)}>
                  <option>Noida</option>
                  <option>Delhi</option>
                  <option>Vadodara</option>
                  <option>Shimla</option>
                  <option>Patna</option>
                  <option>Heydrabad</option>
                  <option>Chandighar</option>
                  <option>Mohali</option>
                  <option>Gurogram</option>
                </Form.Control>                
              </Form.Group>
              <Form.Group controlId="formBasicEmail">
                   <Form.Label>Pin Code</Form.Label>
                  <Form.Control type="business_name"  placeholder="Pin Code" value={this.state.pincode} onChange={(e) => this.handlePinCodeChange(e)} />
              </Form.Group>
              <Form.Group className="col-md-12">
          <Form.Check
            required
            name="terms"
            label="Current address same as Permanent address"
          
          />
        </Form.Group>
          <div className="form-submit">
               <Button className="cta-btn cta-blue" onClick={() => this.update()} >{this.state.updateText}</Button>
              </div>
          </Form>
      </div>
    );
   }
}

export default Address
