import React from 'react';
import { Component } from 'react';
import { Button } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

declare var ithours_client;

class Documents extends Component {

  constructor(props) {
    super(props);
    this.state = {
      uploadPanCard: '',
      uploadAdharCardFront: '',
      uploadAdharCardBack: '',
      latestElectricityBill: '',
      selfPhoto: '',
      updateText: 'Update'
    }
  }
  async componentDidMount() {
    var borrower_id = window.localStorage.getItem("BID");
    let data = await ithours_client.getOne("borrower", {
      _id: borrower_id,
    });
    console.log(data);
    this.state.uploadPanCard = data.apidata.Data.pandocumentfiles[0];
    this.state.uploadAdharCardFront = data.apidata.Data.addharfrontfiles;
    this.state.uploadAdharCardBack = data.apidata.Data.addharbackfiles;
    this.state.latestElectricityBill = data.apidata.Data.electricitybillFiles;
    this.state.selfPhoto = data.apidata.Data.photoinsidefiles;
    // this.setState({})
    console.log(this.state.uploadPanCard);
    console.log(this.state.uploadAdharCardFront);
    console.log(this.state.uploadAdharCardBack);
    console.log(this.state.latestElectricityBill);
    
    console.log("self Photo");
    console.log(this.state.latestElectricityBill);
  }
  async update() {
    toast.dark("Update successful", {
      position: 'bottom-right',
      autoClose: 5000,
      hideProgressBar: true,
    });
    this.state.updateText = 'Updating....';
    //this.setState({})
    var borrower_id = window.localStorage.getItem("BID");
    var findQuery = {
      _id: borrower_id,
    };
    var updateQuery = {
      pandocumentfiles: this.state.uploadPanCard,
      addharfrontfiles: this.state.uploadAdharCardFront,
      addharbackfiles: this.state.uploadAdharCardBack,
      electricitybillFiles: this.state.latestElectricityBill,
      photoinsidefiles: this.state.selfPhoto,
    }
    let updatedRes = await ithours_client.update("borrower", findQuery, updateQuery);
    console.log(updatedRes);
    this.state.updateText = 'Update';
    //this.setState({})
  }
  handleUploadPanCardChange(e) {
    let pan = e.target.value;
    console.log(pan);
    this.state.uploadPanCard = pan;
    //this.setState({});
  };
  handleAddraFrontChange(e) {
    let front = e.target.value;
    console.log(front);
    this.state.uploadAdharCardFront = front;
    //this.setState({});
  };
  handleAdharBackChange(e) {
    let Change = e.target.value;
    console.log(Change);
    this.state.uploadAdharCardBack = Change;
    //this.setState({});
  };
  handleElectricityChange(e) {
    let Bill = e.target.value;
    console.log(Bill);
    this.state.latestElectricityBill = Bill;
   // this.setState({});
  };
  handleselftPhotoChange(e) {
    let Photo = e.target.value;
    console.log(Photo);
    this.state.selfPhoto = Photo;
    //this.setState({   });
  };
  render() {
    return (
      <div>
        <Form>
          <ToastContainer />
          <Form.Group controlId="formBasicEmail">
            <Form.Label>UPLOAD PANCARD</Form.Label>
            <Form.Control type="file" onChange={(e) => this.handleUploadPanCardChange(e)} value={this.state.uploadPanCard} />
          </Form.Group>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>UPLOAD AADHAR FRONT SIDE</Form.Label>
            <Form.Control type="file" onChange={(e) => this.handleAddraFrontChange(e)} value={this.state.uploadAdharCardFront} />
          </Form.Group>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>UPLOAD AADHAR BACK SIDE</Form.Label>
            <Form.Control type="file" onChange={(e) => this.handleAdharBackChange(e)} value={this.state.uploadAdharCardBack} />
          </Form.Group>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>LATEST ELECTRICITY BILL</Form.Label>
            <Form.Control type="file" onChange={(e) => this.handleElectricityChange(e)} value={this.state.latestElectricityBill} />
          </Form.Group>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>SELF PHOTO</Form.Label>
            <Form.Control type="file" onChange={(e) => this.handleselftPhotoChange(e)} value={this.state.selfPhoto} />
          </Form.Group>
          {/* <Form.Group controlId="formBasicEmail">
                   
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Control type="file"/>
                </Form.Group>   */}
          <div className="form-submit">
            <Button className="cta-btn cta-blue" onClick={() => this.update()} >{this.state.updateText}</Button>
          </div>
        </Form>
      </div>
    )
  }
}

export default Documents
