import React, { Component } from "react";
import Slider from "./Slider";
import form_logo from '../public/images/credin-logo.png';
import user_icon from '../public/images/user-icon.svg';
import lock_icon from '../public/images/password-icon.svg';
import mail_icon from '../public/images/mail-icon.svg';
import '../components/login-signup.css';
import '../components/cta-btn.css';
//import { useFormik } from "formik";
//import { Link } from "react-router-dom";
import { Link, Redirect, useHistory} from "react-router-dom";
import { useFormik } from "formik";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

declare var ithours_client;

const SignUp = () => {
    const formik = useFormik({
        initialValues: {
          email: '',
          confirmPassword: '',
          password: '',
          LoginText: 'SIGNUP',
        },
        validate:(values) =>{
            const error = {};
            if (!values.email) {
                error.email = "Please enter email."
            }
            if (values.email) {
                var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
                if (!pattern.test(values.email)) {
                  error.email = "Please enter valid email address.";
                }
            }
            if (values.confirmPassword != values.password) {
                error.confirmPassword = "password and confirm-password does not match."
            }
            if (!values.password) {
                error.password = "Please enter password."
            }
            return error;
        },
        onSubmit:  ( values,
            {
              setSubmitting,
            }) => {
            apicall();
            formik.initialValues.LoginText = 'SIGNUPING....'
                setSubmitting(true);
       //alert(JSON.stringify(values, null, 2));
        },
      });
      const history = useHistory();

      const  apicall = async() => {
         formik.initialValues.LoginText = 'SIGNUPING....'
         
          let datas = await ithours_client.get("User")
          console.log("check")
          console.log(datas);
          let test = datas.apidata.Data.map((e) => e.email); 
          console.log(test)
          console.log(formik.values.email);
         // console.log(formik.values.user_name);
          if(test.indexOf(formik.values.email) == -1){
            let data = await ithours_client.add("User", {
                email: formik.values.email,
                password: formik.values.password,
            });
            console.log(data);

                toast.dark("sign-up has been done successfully.", {
                    position:'bottom-right',
                    autoClose:5000,
                    hideProgressBar:true,
             }); 
             setTimeout(() => {
                history.push(`/dashboard`);
             }, 1000);           
           }
          else{
            toast.dark("Email exist already.", {
                position:'bottom-right',
                autoClose:5000,
                hideProgressBar:true,
            });   
          };              
           console.log("api data");
          //console.log(values.user_name);
       };
    return (
        <div className="account-wrap">
            <div className='row no-gutters'>
                <div className="col-lg-8">
                    <Slider />
                </div>
                <div className="col-lg-4">
                    <div className="form-wrap account-form">
                        <div className="account-form-logo">
                            <img src={form_logo} />
                        </div>
                        <form onSubmit={formik.handleSubmit}>
                          <ToastContainer/>
                            <h3 className="account-form-title">Sign Up</h3>

                            <div className="form-group">
                                <img src={mail_icon} alt="" />
                                <input type="email" value={formik.values.email} onChange={(e) => formik.setFieldValue('email',e.target.value)} className="border border-0" placeholder="EMAIL" />
                            </div>
                            {formik.errors.email &&
                                formik.touched.email && 
                                <span style={{color:"red"}}>{formik.errors.email}</span>}

                
                            <div className="form-group">
                                <img src={lock_icon} alt="" />
                                <input type="password" onChange={(e) => formik.setFieldValue('password',e.target.value)} className="border border-0" placeholder="PASSWORD" />
                            </div>
                            {formik.errors.password &&
                                formik.touched.password && 
                                <span style={{color:"red"}}>{formik.errors.passsword}</span>}
                                            <div className="form-group">
                                <img src={lock_icon} alt="" />
                                <input type="password" value={formik.values.confirmPassword} onChange={(e) => formik.setFieldValue('confirmPassword',e.target.value)} className="border border-0" placeholder="CONFIRM PASSWORD"/>
                            </div>
                            {formik.errors.confirmPassword &&
                                formik.touched.confirmPassword && 
                                <span style={{color:"red"}}>{formik.errors.confirmPassword}</span>}

                            <div className="submit-btn">
                                <button type="submit" className="cta-btn">{formik.initialValues.LoginText}</button>
                            </div>
                            <div className="author-form-note">
                                By creating an account you agree to our <a href="https://credin.shiksha/terms-and-conditions/">Terms of Service</a> and <a href="https://credin.shiksha/privacy-policy/">Privacy Policy</a>
                                <p>If you have already account <Link to="/sign-in">Log In</Link>.</p>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>  
    );
}
export default SignUp